@import url(https://fonts.googleapis.com/css2?family=Skranji:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700;800;900&display=swap);
body, html {
    margin: 0;
    overflow: hidden;
    font-family: 'Cinzel', serif;
    font-weight: 400 !important;
}

.main {
    background-size: cover !important;
    background-color: #FF819E !important;
}

#main-wrapper {
    height: 100vh;
    background: url('/personal_page/img/main.gif') center;
}

#tg-wrapper {
    background: url('/personal_page/img/tg.gif') center;
}

#github-wrapper {
    background: url('/personal_page/img/gh.gif') center;
}

#vk-wrapper {
    background: url('/personal_page/img/vk.gif') center;
}

#layout-opacity {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
}

#menu-wrapper {
    display: flex;
    align-content: center;
    flex-direction: row;
}

#menu {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10rem;
}

.link-wrapper p {
    font-size: 2rem;
    color: white;
}

.link-container {
    border: 1px solid transparent;
    border-radius: 30px;
    padding: 0 0.5rem 0;
    display: flex;
    justify-content: center;
    transition: 0.2s;
}

.link-container:hover {
    border: 1px solid white;
}

.logo-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}

.logo {
    width: 10rem;
    -moz-transition: all 0.1s ease-out;
    -o-transition: all 0.1s ease-out;
    -webkit-transition: all 0.1s ease-out;
    opacity: 0.75;
}

.logo:hover {
    opacity: 1;
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
}

@media (max-width: 768px) {
    #menu {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 1.5rem;
    }

    .link-wrapper p {
        font-size: 1.5rem;
        color: white;
    }

    .main {
        background-size: cover !important;
        /*zoom: 125%*/
        /*transform: scale(1.2);*/
    }

    .logo {
        width: 7rem;
    }
}

p, h1 {
    text-shadow: -.0625rem -.0625rem 0 #6BFFFF, .0625rem .0625rem 0 #FF819E;
}

h1 {
    font-weight: 400 !important;
    /*font-family: 'Skranji', cursive;*/
    color: #ffffff;
    font-size: 2rem;
}

.footer-wrapper {
    margin: 1rem;
    position: absolute;
    width: 100%;
    bottom: 0;
}

.footer-container {
    display: flex;
    justify-content: center;
}

.footer-container h1 {
    display: flex;
    justify-content: center;
    letter-spacing: 1rem !important;
}

@media (max-width: 425px) {
    .logo {
        width: 5rem;
    }

    .footer-container h1 {
        display: flex;
        justify-content: center;
        letter-spacing: 0.4rem !important;
    }
}

a {
    color: transparent;
    text-decoration: none;
}

